import myrequest from '@/utils/axios'

import { msapiOnline } from "./onLineBaseUrl";

//考勤查询
export function GetPageContentByConditions (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_checkwork/GetPageContentByConditions',
    method: 'post',
    data: params
  })
}
//新增考勤
export function AddItem (params) {
  return myrequest({
    url: msapiOnline + '/msapi/v1.0/YZProjectService/tb_project_checkwork/AddItem',
    method: 'post',
    data: params
  })
}