 * @Github: https://github.com/hzylyh
 * @Date: 2020-12-10 09:37:25
 * @LastEditors: yanxiao
-->
<!-- 签到 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">签到</p>
      </van-col>
    </van-row>
    <van-row class="dt-content">
      <div id="container"></div>
      <div class="signHistory"
           @click="popShow=true">
        <van-icon name="edit"
                  class="signIco" />签到记录
      </div>
      <div class="dt-sign-box">
        <van-row class="dt-signIn">
          <div class="dt-signIn-button"
               @click="signIn">
            <span>{{statusText}}</span>
          </div>
        </van-row>
        <div class="sign-text">
          <div><span style="background: #7CE574"></span>签到时间：{{this.checkIn | filtDate}}</div>
          <div><span style="background: #F45A5A"></span>签退时间：{{this.checkOut | filtDate}}</div>
        </div>
      </div>

    </van-row>
    <van-popup v-model="popShow"
               position="bottom"
               round
               :style="{ height: '50%' }">
      <van-row class="background-image">
        <van-row class="dt-table">
          <van-calendar :show-title="false"
                        :poppable="false"
                        :show-confirm="false"
                        :show-mark="false"
                        :min-date="minDate"
                        :max-date="maxDate"
                        :default-date="CheckDateList"
                        type="multiple"
                        color="#E3ECFD"
                        :style="{  height: 'px2rem(500)' }"
                        :formatter="formatter"
                        readonly />
        </van-row>
      </van-row>
    </van-popup>
  </van-row>
</template>
<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import {
  GetPageContentByConditions,//查询考勤接口
  AddItem//新增考勤接口
} from "@/api/attendanceManagement";
import { getDepartment } from "@/utils/user";
import { WGTfGc } from "@/utils/map";

// 主园区（包含P2 P3停车场）
const dot1 = [32.338702, 119.082536] // 左上
const dot2 = [32.323029, 119.109109]  // 右下
const dot3 = [32.336605, 119.106966] // 右上
const dot4 = [32.327299, 119.082039]  // 左下

// P1停车场（左下）
const P1Dot1 = [32.32681, 119.084153] // 左上
const P1Dot2 = [32.320722, 119.090813]  // 右下
const P1Dot3 = [32.325379, 119.092311] // 右上
const P1Dot4 = [32.323005, 119.083512]  // 左下

// P4停车场（左上）
const P4Dot1 = [32.342202, 119.083905] // 左上
const P4Dot2 = [32.338726, 119.087902]  // 右下
const P4Dot3 = [32.341955, 119.087824] // 右上
const P4Dot4 = [32.338857, 119.084041]  // 左下

const LATEXCUR = -0.002
const LONEXCUR = 0.0055

const H5lonLat = { "latitude": 32.328552, "longitude": 119.104515, address: '江苏省' }
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  filters: {
    filtDate (date) {
      date = date.replace(new RegExp("-", "gm"), '/');
      if (date == '未签到' || date == '未签退') {
        return date
      } else {
        return new Date(date).format("hh:mm:ss")
      }
    }
  },
  data () {
    //这里存放数据
    return {
      information: JSON.parse(localStorage.getItem("userInfo")),
      //内容
      formIn: {
        CheckUserId: JSON.parse(localStorage.getItem("userInfo")).uid,//usid
        CheckUserName: JSON.parse(localStorage.getItem("userInfo")).real_name,//姓名
        // CheckUserNo: '',//工号
        CheckUserDept: '',//部门
        CheckUserDuty: '',//职责
        CheckStatus: 0,//签到状态(0.签到，1.签退)
        CheckDate: null,//考勤日期
        CheckTime: null,//签到时间
        //SignOutTime: null,//签退时间

        longitude: '',//经度
        latitude: '',//纬度
        Address: '',//打卡地址
        CheckResource: 1,//考勤来源(1.app,2.考勤机3.中青旅)
        LinkPhone: JSON.parse(localStorage.getItem("userInfo")).mobile,//手机号
        // Creater: '',//创建人
        // CreateTime: '',//创建时间
        // Modifier: '',//修改人
        // ModifyTime: '',//ModifyTime
        Validity: 1//是否有效（0.无效，1.有效）
      },
      formOut: {
        CheckUserId: JSON.parse(localStorage.getItem("userInfo")).uid,//usid
        CheckUserName: JSON.parse(localStorage.getItem("userInfo")).real_name,//姓名
        // CheckUserNo: '',//工号
        // CheckUserDept: '',//部门
        CheckUserDuty: '',//职责
        CheckStatus: 1,//签到状态(0.签到，1.签退)
        CheckDate: null,//考勤日期
        //CheckTime: null,//签到时间
        SignOutTime: null,//签退时间
        longitude: '',//经度
        latitude: '',//纬度
        Address: '',//打卡地址
        CheckResource: 1,//考勤来源(1.app,2.考勤机3.中青旅)
        LinkPhone: JSON.parse(localStorage.getItem("userInfo")).mobile,//手机号
        // Creater: '',//创建人
        // CreateTime: '',//创建时间
        // Modifier: '',//修改人
        // ModifyTime: '',//ModifyTime
        Validity: 1//是否有效（0.无效，1.有效）
      },
      checkList: [],//查询返回列表
      show: false,
      currentDate: new Date(),
      minDate: new Date(2021, 0, 1),
      maxDate: new Date(2030, 11, 31),
      CheckDateList: [],//已考勤日期列表
      signOutList: [],//签退日期
      signOutDate: [],//格式化签退时间
      statusText: '',
      contentList: [],//考勤日期-查询返回
      statussss: -1,
      tttoday: null,
      checkIn: '未签到',
      checkOut: '未签退',
      in: [],//
      out: [],//
      phonelat: null,//手机纬度
      phonelon: null,//手机经度
      inLat: '',//签到纬度
      inLon: '',//签到经度
      outLat: '',//签到经度
      outLon: '',//签到纬度
      inAddress: '未签到',//签到地址
      outAddress: '未签退',//签退地址
      // 覆盖物 签到范围
      polygon: null,

      // 弹出层
      popShow: false
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {

  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initMap()
    this.getAttendance()

    this.getDepartment()
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {

    initMap () {
      var center = new window.TMap.LatLng(32.332700, 119.088555);

      //初始化地图
      let map = new window.TMap.Map("container", {
        zoom: 13.5,//设置地图缩放级别
        center: center,//设置地图中心点坐标
        disableDefaultUI: true
      });
      var path = [ //多边形轮廓点串（LatLng数组）
        new window.TMap.LatLng(dot1[0] - LATEXCUR, dot1[1] - LONEXCUR),


        new window.TMap.LatLng(P4Dot4[0] - LATEXCUR, P4Dot4[1] - LONEXCUR),
        new window.TMap.LatLng(P4Dot1[0] - LATEXCUR, P4Dot1[1] - LONEXCUR),
        new window.TMap.LatLng(P4Dot3[0] - LATEXCUR, P4Dot3[1] - LONEXCUR),
        new window.TMap.LatLng(P4Dot2[0] - LATEXCUR, P4Dot2[1] - LONEXCUR),


        new window.TMap.LatLng(dot3[0] - LATEXCUR, dot3[1] - LONEXCUR),
        new window.TMap.LatLng(dot2[0] - LATEXCUR, dot2[1] - LONEXCUR),

        new window.TMap.LatLng(P1Dot3[0] - LATEXCUR, P1Dot3[1] - LONEXCUR),
        new window.TMap.LatLng(P1Dot2[0] - LATEXCUR, P1Dot2[1] - LONEXCUR),
        new window.TMap.LatLng(P1Dot4[0] - LATEXCUR, P1Dot4[1] - LONEXCUR),
        new window.TMap.LatLng(P1Dot1[0] - LATEXCUR, P1Dot1[1] - LONEXCUR),

        new window.TMap.LatLng(dot4[0] - LATEXCUR, dot4[1] - LONEXCUR),
      ]
      // 初始化polygon(园区)
      new window.TMap.MultiPolygon({
        id: 'polygon-layer', //图层id
        map: map, //设置多边形图层显示到哪个地图实例中
        //多边形样式
        styles: {
          'polygon': new window.TMap.PolygonStyle({
            'color': 'rgba(0,125,255,0.3)', //面填充色
            'showBorder': false, //是否显示拔起面的边线
            'borderColor': '#00FFFF' //边线颜色
          })
        },
        //多边形数据
        geometries: [
          {
            'id': 'p1', //该多边形在图层中的唯一标识（删除、更新数据时需要）
            'styleId': 'polygon', //绑定样式名
            'paths': path, //多边形轮廓
          }
        ]
      });

      let lat = 0.0 // 纬度
      let lon = 0.0 // 经度
      if (window.android) {
        let lonLat = JSON.parse(window.android.getLocation().toLocaleString());
        lat = lonLat.latitude
        lon = lonLat.longitude
        console.log("安卓定位返回！！！", lat, lon);
      } else if (window.webkit) {
        let ioslonlat = JSON.parse(localStorage.getItem("location"))
        lat = ioslonlat.latitude
        lon = ioslonlat.longitude
        const tflonlat = WGTfGc(lon, lat)
        lon = tflonlat[0]
        lat = tflonlat[1]
        console.log("ioslonlat定位返回！！！", lat, lon);
      } else {
        lat = H5lonLat.latitude
        lon = H5lonLat.longitude
        console.log("h5定位返回！！！", lat, lon);
      }
      //创建并初始化MultiMarker
      new window.TMap.MultiMarker({
        map: map,  //指定地图容器
        //样式定义
        styles: {
          //创建一个styleId为"myStyle"的样式（styles的子属性名即为styleId）
          "myStyle": new window.TMap.MarkerStyle({
            "width": 25,  // 点标记样式宽度（像素）
            "height": 35, // 点标记样式高度（像素）
            // "src": '../img/marker.png',  //图片路径
            //焦点在图片中的像素位置，一般大头针类似形式的图片以针尖位置做为焦点，圆形点以圆心位置为焦点
            "anchor": { x: 16, y: 32 }
          })
        },
        //点标记数据数组
        geometries: [{
          "id": "1",   //点标记唯一标识，后续如果有删除、修改位置等操作，都需要此id
          "styleId": 'myStyle',  //指定样式id
          "position": new window.TMap.LatLng(lat - LATEXCUR, lon - LONEXCUR),  //点标记坐标位置
          "properties": {//自定义属性
            "title": "marker1"
          }
        }]
      });

    },

    /**
     * 判断点是否在区域内
     * params (纬度lat, 经度lon)
     * 返回 bool
     */
    checkSite (lat, lon) {
      let isContains = false
      if (lat >= dot2[0] && lat <= dot1[0] && lon >= dot1[1] && lon <= dot2[1]) {
        console.log("在园区范围内");
        isContains = true
      } else if (lat >= P1Dot2[0] && lat <= P1Dot1[0] && lon >= P1Dot1[1] && lon <= P1Dot2[1]) {
        console.log("在P1停车场范围内");
        isContains = true
      } else if (lat >= P4Dot2[0] && lat <= P4Dot1[0] && lon >= P4Dot1[1] && lon <= P4Dot2[1]) {
        console.log("在P4停车场范围内");
        isContains = true
      } else {
        console.log("不再范围内");
        isContains = false
      }
      return isContains
    },
    // 签到按钮
    signIn () {
      if (window.android) {
        console.log("安卓");
        //手机端
        let lonLat = JSON.parse(window.android.getLocation().toLocaleString());
        console.log("安卓定位返回！！！", lonLat);
        this.phonelat = lonLat.latitude
        this.phonelon = lonLat.longitude
        if (this.checkSite(this.phonelat, this.phonelon)) {
          console.log("安卓在范围内");
          this.formIn.longitude = String(lonLat.longitude);
          this.formOut.longitude = String(lonLat.longitude);
          this.formIn.latitude = String(lonLat.latitude);
          this.formOut.latitude = String(lonLat.latitude);
          this.formIn.Address = String(lonLat.address);
          this.formOut.Address = String(lonLat.address);
          this.handleSign()
        }
        else {
          console.warn('安卓不在签到范围内');
          this.$toast({
            message: '不在打卡范围',
            position: 'top'
          })
        }

      } else if (window.webkit) {
        console.log("苹果");
        let ioslonlat = JSON.parse(localStorage.getItem("location"))
        let lonLat = ioslonlat
        this.phonelat = lonLat.latitude
        this.phonelon = lonLat.longitude
        if (this.checkSite(this.phonelat, this.phonelon)) {
          console.log("IOS在范围内");
          this.formIn.longitude = String(lonLat.longitude);
          this.formOut.longitude = String(lonLat.longitude);
          this.formIn.latitude = String(lonLat.latitude);
          this.formOut.latitude = String(lonLat.latitude);
          this.formIn.Address = String(lonLat.address);
          this.formOut.Address = String(lonLat.address);
          this.handleSign()
        }
        else {
          console.warn('IOS不在签到范围内');
          this.$toast({
            message: '不在打卡范围',
            position: 'top'
          })
        }
      } else {
        console.log("H5");
        this.phonelat = H5lonLat.latitude
        this.phonelon = H5lonLat.longitude
        if (this.checkSite(this.phonelat, this.phonelon)) {
          console.log("H5在范围内");
          this.formIn.longitude = String(H5lonLat.longitude);
          this.formOut.longitude = String(H5lonLat.longitude);
          this.formIn.latitude = String(H5lonLat.latitude);
          this.formOut.latitude = String(H5lonLat.latitude);
          this.handleSign()
        } else {
          console.warn('H5不在签到范围内');
          this.$toast({
            message: '不在打卡范围',
            position: 'top'
          })
        }
      }
    },

    //获取部门
    getDepartment () {
      getDepartment(data => {
        this.formIn.CheckUserDept = data.name
        this.formIn.CheckUserDuty = data.name
        this.formOut.CheckUserDept = data.name
        this.formOut.CheckUserDuty = data.name
      })
    },

    // 考勤新增
    attendanceIn () {//签到
      console.log("新增考勤请求", this.formIn);
      AddItem(this.formIn).then(response => {
        console.log("新增考勤返回", response);
        this.getAttendance()
        this.$toast.success({
          message: '签到成功',
        })
      })
    },
    attendanceOut () {//签退
      console.log("新增考勤请求", this.formOut);
      AddItem(this.formOut).then(response => {
        console.log("新增考勤返回", response);
        this.getAttendance()
        this.$toast.success({
          message: '签退成功',
        })
      })
    },

    //查询考勤
    getAttendance () {
      GetPageContentByConditions({
        "PageSize": 9999999,
        "CheckUserId": this.information.uid,
        "PageIndex": 1,
      }).then(response => {
        console.log("获取到的数据", response)
        this.checkList = response.Data.ContentList
        for (let index in this.checkList) {      //遍历checkList
          let check = this.checkList[index].CheckDate
          this.CheckDateList.push(new Date(check));//CheckDateList
        }
        for (let index in this.checkList) {      //遍历checkList
          let signOut = this.checkList[index].SignOutTime
          this.signOutList.push(new Date(signOut));//signOutList
        }
        this.panduan()
      })
    },

    handleSign () {
      if (this.statusText == "签到") {
        var aData = new Date();
        console.log("签到日期", aData)
        this.formIn.CheckTime = aData
        this.formIn.CheckDate = this.toDay()
        this.attendanceIn()//调用新增考勤接口-签到

      } else if (this.statusText == "签退") {
        this.$dialog.confirm({
          message: '确认签退',
        })
          .then(() => {
            var bData = new Date();
            console.log("签退日期", bData)
            this.formOut.SignOutTime = bData
            this.formOut.CheckDate = this.toDay()
            this.attendanceOut()//调用新增考勤接口-签退

          })
          .catch(() => {
            // on cancel
          });
      } else if (this.statusText == "已签退") {
        this.$toast.success({
          message: '已签退',
        })
      }
    },

    //判断
    panduan () {
      //未签到
      GetPageContentByConditions({
        "PageSize": 99999,
        "PageIndex": 1,
        "CheckUserId": this.information.uid,
        "CheckDate": this.toDay()
      }).then(response => {
        if (response.Data.ContentList) {
          this.contentList = response.Data.ContentList
          for (let index in this.contentList) {
            let status = this.contentList[index].CheckStatus
            if (status == 0) {
              this.statussss = 0
            } else if (status == 1) {
              this.statussss = 1
              break
            }
          }
        } else {
          this.statussss = -1
        }
        if (this.statussss == -1) {
          this.statusText = "签到"
        } else if (this.statussss == 0) {
          //已签到-----------------------------------------已签到
          this.statusText = "签退"
          //签到信息
          GetPageContentByConditions({
            "PageSize": 99999,
            "PageIndex": 1,
            "CheckUserId": this.information.uid,
            "CheckDate": this.toDay(),
            "CheckStatus": 0
          }).then(response => {
            this.in = response.Data.ContentList
            let aa = this.in[0].CheckTime
            let i = new Array();
            i = aa.split("T")
            this.checkIn = i[0] + " " + i[1]
            //经纬度
            this.inLon = this.in[0].longitude
            this.inLat = this.in[0].latitude
            //打卡地址
            this.inAddress = this.in[0].Address
          })
        } else if (this.statussss == 1) {
          //已签退-----------------------------------------已签退
          this.statusText = "已签退"
          //签到信息
          GetPageContentByConditions({
            "PageSize": 99999,
            "PageIndex": 1,
            "CheckUserId": this.information.uid,
            "CheckDate": this.toDay(),
            "CheckStatus": 0
          }).then(response => {
            this.in = response.Data.ContentList
            let aa = this.in[0].CheckTime
            let i = new Array();
            i = aa.split("T")
            this.checkIn = i[0] + " " + i[1]
            //经纬度
            this.inLon = this.in[0].longitude
            this.inLat = this.in[0].latitude
            //打卡地址
            this.inAddress = this.in[0].Address
          })
          //签退信息
          GetPageContentByConditions({
            "PageSize": 99999,
            "PageIndex": 1,
            "CheckUserId": this.information.uid,
            "CheckDate": this.toDay(),
            "CheckStatus": 1
          }).then(response => {
            this.out = response.Data.ContentList
            let aa = this.out[0].SignOutTime
            let i = new Array();
            i = aa.split("T")
            this.checkOut = i[0] + " " + i[1]
            //经纬度
            this.outLon = this.out[0].longitude
            this.outLat = this.out[0].latitude
            //打卡地址
            this.outAddress = this.out[0].Address
          })
        }
      })

    },

    //当前日期
    toDay () {
      var tDay = new Date();
      var year = tDay.getFullYear();
      var month = tDay.getMonth();
      var day = tDay.getDate();
      let nowTime = new Date(year, month, day);
      this.tttoday = day;
      return nowTime
    },
    // 返回首页
    Home () {
      this.$router.push({ name: 'Home' })
    },

    //日历
    formatter (day) {
      const year = day.date.getFullYear();
      const month = day.date.getMonth();
      const date = day.date.getDate();
      for (let index in this.signOutList) {
        let yy = new Date(this.signOutList[index]).getFullYear();
        let mm = new Date(this.signOutList[index]).getMonth();
        let dd = new Date(this.signOutList[index]).getDate();
        if (year === yy) {
          if (month === mm) {
            if (date === dd) {
              day.bottomInfo = '●'
            }
          }
        }
      }
      return day;
    },

    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
@import '../../../styles/dimens';
//@import url(); 引入公共css类
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
